function prepareContent() {
    var s = window.stbUPnP,
        result = [],
        item;

    while ( (item = list.shift()) ) {
        switch ( item.ContentType ) {
            case s.TYPE_FOLDER:
                item.type = 9;
                item.url = item.ID;
                break;
            case s.TYPE_AUDIO:
                item.type = 6;
                break;
            case s.TYPE_VIDEO:
                item.type = 5;
                break;
            case s.TYPE_IMAGE:
                item.type = 7;
                break;
            default :
                item.type = 8;
                break;
        }

        if ( item.type !== s.TYPE_FOLDER && item.Resources && item.Resources.length ) {
            item.url = item.Resources[0].Value;
            item.size = item.Resources[0].Size;
        }
        item.Resources = null;
        item.readOnly = true;
        item.name = item.Title;

        result.push(item);
    }

    return result;
}

