'use strict';

var app = require('./stb/app');

app.addListeners({
    load: function load () {
        var parallel = require('cjs-async/parallel'),
            gettext  = require('./stb/gettext'),
            router   = require('./stb/router'),
            config   = require('../../config/app'),
            network  = require('./modules/network');

        app.network = network(config);

        gettext.load({name: core.environment.language || 'en'}, function ( err ) {
            if ( err ) {
                debug.log('Gettext loading is failed...................', 'red');
            }
        });

        router.init([
            require('./pages/main')
        ]);

        if ( window.parent !== window ) {
            app.show();
        }

        router.navigate('pageMain');
    }
});



